*{
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  background-color:rgb(232, 221, 221);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heading{
  position:sticky;
  top:0;
  text-align: center;
  color: white;
  background-color: rgb(22, 22, 94);
  border:5px solid black;
  padding: 10px;
  border-radius: 20px;
  margin-bottom: 10px;
}